	module.exports = {"data":{"markdownRemark":{"html":"<p>これまで耳コピばかりしかやっていなかったが、この辺からオリジナル楽曲やアレンジ楽曲なんかも作り始めるようになる。 ソフトウェア XG 音源。当時は YAMAHA 派だったし、結局手にすることはなかったものの、 MU128 が喉から手が出る程に欲しいお年頃だった。  Roland 派に改宗したのは Izeef (当時さくらやまスクエア) の <a href=\"http://sky.geocities.jp/izeefss/izeef/\">Shou 氏</a>と東方 Project の <a href=\"http://www16.big.or.jp/%7Ezun/\">ZUN 氏</a>による影響が大きい。でもそろそろ YAMAHA と復縁しそうな感じ。 (VOCALOID が出るずっと前から PLG-100SG に強く興味持ってたし) <a href=\"/pc-9821\">98 の FM 音源</a>に慣れていた自分には、 SB16 の FM 音源がイマイチ肌に合わずこちらを使っていたが、当時としては無茶苦茶に重かった。多分今でも重そう。</p>","frontmatter":{"cover":null,"date":"1999-05-01T00:00:00+09:00","strDate":"1999/4/30","title":"(1999-2000) YAMAHA S-YXG50"}}},"pathContext":{"path":"/ja/blog/1999/05/01-s-yxg50/","langKey":"ja"}}

